
body, ul{
    padding: 0;
    margin: 0;
}
.alphabet{
    position: fixed;
    right: 0;
    top: 100px;
}
.list-group-header {
    background: #f2f2f2;
    height: 30px;
    padding: 0 20px;
    line-height: 30px;
}
.list-item{
    border-bottom: solid 1px #f2f2f2;
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
}
.list-item:last-child{
    border-bottom: none;
}
